<template>
    <div class="card">
        <div class="card-header pb-0">
            <h6>Data Form Peserta</h6>
        </div>
      <div class="card-body px-0 pt-0 pb-2">
        <div class="table-responsive p-0">
          <form role="form" @submit.prevent="submit">
          <table class="table align-items-center mb-0">
            <div class="card-body">
                <div class="row">
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label">Penjemput</label>
                    <input v-model="penjemput" class="form-control" type="text" placeholder="Masukkan nama Penjemput" required />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label">LO</label>
                    <input v-model="lo" class="form-control" type="text" placeholder="Masukkan nama LO" required />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label">Instagram Penjemput (Link)</label>
                    <input v-model="ig1" class="form-control" type="text" placeholder="Masukkan link instagram Penjemput (harus link)" required />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label">Instagram LO (Link)</label>
                    <input v-model="ig2"  class="form-control" type="text" placeholder="Masukkan link Instagram LO (harus link)" required />
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label">Whatsapp Penjemput (Link)</label>
                    <input v-model="wa1" class="form-control" type="text" placeholder="Masukkan link Whatsapp Penjemput (harus link)" required/>
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label">WhatsApp LO (Link)</label>
                    <input v-model="wa2" class="form-control" type="text" placeholder="Masukkan link Whatsapp lo (harus link)" required/>
                  </div>
                  <div class="col-md-6">
                    <label for="example-text-input" class="form-control-label">Klotter</label>
                    <input v-model="kloter" class="form-control" type="text" placeholder="Masukkan klotter peserta" required/>
                  </div>
                </div>
              </div>
            </table>
            <div class="col-6 text-home px-4 pb-4">
              <argon-button  type="submit" color="dark" variant="gradient" @click="submit">Submit</argon-button>
            </div>
          </form>
         </div>
      </div>
    </div>
    </template>
    
    <script>
    /* eslint-disable */
    import axios from 'axios';
    import ArgonButton from "@/components/ArgonButton.vue";
  
    export default {
      name: "Berita",
      props: {
        id: {
      type: Number,
      required: true
      }
    },
      components: {
      ArgonButton,
    },
    data() {
      return {
        imageUrl: '',
        imageAlt: '',
        data: {
          user:{
            name: '',
            email: '',
            phone: '',
            tiket: '',
            photo: '',
            ktm: '',
            transfer: '',
            penjemput: '',
            lo: '',
            ig1: '',
            ig2: '',
            wa1: '',
            wa2: '',
            kloter: ''
            },
        }
      } 
    },
    methods: {
      submit() {
        if (!this.penjemput || !this.lo || !this.ig1 || !this.ig2 || !this.wa1 || !this.wa2 || !this.kloter) {
          alert('Harap isi semua field yang dibutuhkan!');
          return;
        }
        const url = `http://127.0.0.1:8000/api/penjemputan/${this.$route.params.id}`;
        const data = {
          penjemput: this.penjemput,
          lo: this.lo,
          ig1: this.ig1,
          ig2: this.ig2,
          wa1: this.wa1,
          wa2: this.wa2,
          kloter: this.kloter
        };

        axios.post(url, data)
          .then(response => {
            console.log(response);
            // handle response here
            this.$router.push('/logistik'); // Navigate to "/logistik" path
          })
          .catch(error => {
            console.log(error);
            // handle error here
          });
      }
    },
    mounted() {
        axios.get(`http://127.0.0.1:8000/api/users/${this.$route.params.id}`)
        .then(response => {
          this.data.user = response.data.data;
          this.penjemput = data.user.penjemput;
          this.lo = data.user.lo;
          this.ig1 = data.user.ig1;
          this.ig2 = data.user.ig2;
          this.wa1 = data.user.wa1;
          this.wa2 = data.user.wa2;
          this.kloter = data.user.kloter;
        })
        .catch(error => {
          console.error(error);
        });
      }
    };
    
  </script>